@media(max-width:700px) {
    .home {
        margin-top: 6vh;
        min-height: 70vh;
    }

    .projectWidget {
        gap: 5px;
        place-items: center;
        width: 80%;
        height: 100%;
        padding: 10px;
        flex-direction: column;
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);

    }

    .projectWidget>img {
        width: 100%;
        /* min-height: 50%; */

    }

    .projectWidget>div {
        width: 100%;
        pointer-events: none;
        max-height: 50%;
        /* background-color: blue; */
    }


}